/* Style the tab */
.tab {
  padding-top: 0px;
  float: left;
  border-radius: 15px;
  border: 1px solid #ccc;
  background-color: #6bcae2;
  width: 20%;
  height: 393px;
}

/* Style the buttons inside the tab */
.tab button {
  display: block;
  background-color: inherit;
  color: black;
  padding: 22px 16px;
  width: 100%;
  border: none;
  outline: none;
  text-align: left;
  cursor: pointer;
  transition: 0.3s;
  font-size: 17px;
}

/* Change background color of buttons on hover */
.tab button:hover {
  background-color: #c7d8e7;
}

/* Create an active/current "tab button" class */
.tab button.active {
  background-color: #0d659d;
}

/* Style the tab content */
.tabcontent {
  margin-top: 15px;
  float: left;
  padding: 0px 0px;
  border: 1px solid #ccc;
  border-radius: 15px;
  width: 60%;
  height: 300px;
  margin-left: 60px;
  padding-left: 2%;
}
